/* ============================================================================
 * START Slider
 * ============================================================================ */
.slider-wrapper {
  position: relative;
  .slick.slick-main {
    margin-bottom: 0;
    height: 640px;
    // display: none
    opacity: 0;
    @include r(1024) {
      height: 428px; }
    @include r(768) {
      height: 384px; }
    .slick__item-wrapper {
      background: linear-gradient(0deg, rgba(0, 18, 9, 0.45), rgba(0, 18, 9, 0.45));
      height: 640px;
      background-repeat: no-repeat;
      // background-size: cover
      background-position: center center;
      background-size: 100% auto;
      position: relative;
      @include r(1100) {
        background-size: auto; }
      @include r(1024) {
        height: 428px; }
      @include r(768) {
        height: 384px; }
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-image: linear-gradient(0deg, rgba(0, 18, 9, 0.45), rgba(0, 18, 9, 0.45)); } } }
  .container.dots {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 80px;
    right: 0;
    @include r(1024) {
      bottom: 60px; }
    @include r(768) {
      bottom: 50px; }
    @include r(600) {
      bottom: 60px; } }
  .slick-dots {
    width: auto;
    // li
    //   button:before
    //     position: absolute
    //     background-color: #fff
    //     content: ''
    //     height: 9.33px
    //     width: 9.33px
    //     border-radius: 50px
    //     background-color: #fff
    //     left: 0
    //     top: 0
    //     +trans
    //   button:after
    //     position: absolute
    //     width: 15px
    //     height: 15px
    //     top: -2.7px
    //     left: -3px
    //     font-size: 6px
    //     line-height: 20px
    //     text-align: center
    //     opacity: .25
    //     background-color: transparent
    //     content: ''
    //     border-radius: 50px
    //     border: 1px solid #ffffff
    //     +trans
    // li.slick-active
    //   button:before
    //     background-color: $green1
    //   button:after
    //     opacity: 0.75
    //     border: 1px solid $green1
    //     top: -6px
    //     left: -6px
    //     width: 21px
    //     height: 21px
    .animsvgwrapper {
      .progress {
        transform: rotate(-90deg);
        height: 14px;
        width: 14px;
        margin-bottom: 3.5px;
        @include trans; }
      .progress__value {
        stroke-dasharray: 339.292;
        stroke-dashoffset: 339.292;
 }        // animation: progress 2s infinite alternate
      @keyframes progress {
        from {
          stroke-dashoffset: 339.292; }
        to {
          stroke-dashoffset: 0; } } } }
  .slick-dots {
    .slick-active {
      .animsvgwrapper {
        .progress {
          transform: rotate(-90deg);
          height: 21px;
          width: 21px;
          margin-bottom: 0; }
        .progress__value {
          stroke-dasharray: 339.292;
          stroke-dashoffset: 339.292;
          animation: progress 5s; }
        .progress__inside {
          fill: #00BA3C; }
        @keyframes progress {
          from {
            stroke-dashoffset: 339.292; }
          to {
            stroke-dashoffset: 0; } } } } }
  .slick__text {
    position: absolute;
    color: #fff;
    @include fm;
    font-size: 60px;
    @include maw(840);
    margin-top: 160px;
    @include r(1024) {
      font-size: 50px;
      margin-top: 100px;
      max-width: 90%; }
    @include r(768) {
      margin-top: 0px; }
    @include r(600) {
      margin-top: 100px;
      font-size: 25px; } }
  .slick.slick-main.slick-initialized {
    // display: block
    opacity: 1;
    transition: opacity .3s ease-out; } }
/* ============================================================================
 * END Slider
 * ============================================================================ */
/* ============================================================================
 * START Common
 * ============================================================================ */
.section-header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 80px;
  width: 100%;
  @include r(1024) {
    margin-bottom: 60px; }
  @include r(375) {
    margin-bottom: 40px; }
  h3 {
    @include fm;
    font-size: 36px;
    @include r(1024) {
      font-size: 30px; }
    @include r(550) {
      font-size: 20px; } } }
.btn-more {
  background-color: $gray7;
  display: flex;
  align-items: center;
  line-height: 50px;
  padding: 0 20px;
  @include fs;
  font-size: 14px;
  @include r(450) {
    display: none; }
  svg {
    margin-left: 20px; } }
.btn-more.mobile {
  position: relative;
  display: none;
  width: 227px;
  margin-left: auto;
  margin-right: auto;
  justify-content: spase-between;
  @include r(450) {
    display: flex;
    margin-bottom: 40px; }
  svg {
    position: absolute;
    right: 20px; } }
/* ============================================================================
 * END Common
 * ============================================================================ */

