/* ============================================================================
 * START Industry block
 * ============================================================================ */
.industries-wrapper {
  margin-bottom: 120px;
  margin-top: 80px;
  @include r(1024) {
    margin-bottom: 68px; }
  @include r(786) {
    margin-bottom: -20px;
    margin-top: 60px; }
  @include r(375) {
    margin-bottom: -50px;
    margin-top: 40px; }
  .container {
    clear: both;
    .right-part {
      width: 880px;
      float: right;
      position: relative;
      @include r(1300) {
        width: 710px; }
      @include r(900) {
        display: block;
        width: 100%; }
      &:before {
        content: url('../img/mainpagebag.png');
        position: absolute;
        left: -480px;
        top: -100px;
        @include r(1300) {
          left: -380px; }
        @include r(900) {
          display: none; } }
      h3 {
        @include fm;
        font-size: 36px;
        line-height: 1.2;
        margin-bottom: 120px;
        @include r(1024) {
          font-size: 30px; }
        @include r(375) {
          font-size: 20px; }
        @include r(504) {
          margin-bottom: 60px; } } } } }
/* ============================================================================
 * END Industry block
 * ============================================================================ */
/* ============================================================================
 * START News block
 * ============================================================================ */
section.news-wrapper {
  .container {
    overflow: hidden; } }
.news {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0 -20px 80px;
  // +r(1024)
  //   margin: 0 -20px 60px
  @include r(904) {
    justify-content: center; }
  @include r(450) {
    margin-bottom: 20px; }
  .mobile {
    margin-top: 20px; }
  .news__item {
    position: relative;
    width: 400px;
    height: 400px;
    overflow: hidden;
    border-radius: 3px;
    background-color: $green2;
    margin: 0 20px 40px;
    padding: 40px;
    @include r(375) {
      width: 335px;
      height: 335px;
      margin-bottom: 35px; } }
  .news__content {
    z-index: 1;
    position: absolute;
    padding-right: 20px;
    left: 40px;
    right: 20px;
    top: 40px;
    bottom: 40px;
    color: #ffffff;
    line-height: 1.2;
    font-size: 18px;
    &:after {
      content: '';
      width: 60px;
      height: 2px;
      background-color: #fff;
      position: absolute;
      bottom: 0;
      left: 0; }
    @include r(768) {
      font-size: 16px; }
    @include r(375) {
      font-size: 14px; }
    .header {
      display: flex;
      justify-content: space-between;
      margin-bottom: 39px;
      &__left {
        @include fm;
        font-size: 48px;
        @include r(768) {
          font-size: 40px; }
        span {
          font-size: 14px;
          display: block;
          @include r(768) {
            font-size: 12px; } } }
      &__right {
        background-image: url('../img/newsicon.svg');
        height: 28px;
        width: 28px; } } }
  .news__bg {
    height: 200px;
    width: 200px;
    background-color: $green4;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 0;
    @include r(768) {
      height: 170px;
      width: 170px; }
    @include r(375) {
      height: 165px;
      width: 165px; } } }
/* ============================================================================
 * END News block
 * ============================================================================ */
/* ============================================================================
 * START Connect block
 * ============================================================================ */
.connect-wrapper {
  h3 {
    font-size: 36px;
    line-height: 1.2;
    text-align: center;
    @include fr;
    margin-bottom: 119px;
    @include r(1024) {
      margin-bottom: 100px;
      font-size: 30px; }
    @include r(375) {
      font-size: 20px; } }
  .questions {
    background-color: $green1;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #ffffff;
      @include r(800) {
        flex-direction: column; }
      .left-part {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-grow: 1;
        @include r(1140) {
          flex-direction: column;
          align-items: flex-start; }
        @include r(800) {
          align-items: center; }
        .left-part__one {
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-grow: 1;
          svg {
            margin: 15px 20px 15px 0; }
          h6 {
            @include fr;
            font-size: 24px;
            @include r(768) {
              font-size: 20px; } } }
        .left-part__two {
          margin-left: 10%;
          @include r(1140) {
            margin-left: 0; }
          p {
            font-size: 16px;
            @include r(1140) {
              margin-bottom: 30px; }
            @include r(800) {
              text-align: center; }
            @include r(768) {
              font-size: 14px; } } } }
      form {
        display: flex;
        border-radius: 3px;
        overflow: hidden;
        margin-left: 5%;
        @include r(1140) {
          margin-left: 0; }
        @include r(800) {
          margin-bottom: 40px; }
        input {
          height: 40px;
          text-indent: 30px;
          @include r(385) {
            text-indent: 20px; } }
        .btn {
          background-color: #fff;
          height: 40px;
          padding-right: 30px;
          color: $green1;
          font-size: 15px;
          line-height: 15px;
          @include r(385) {
            padding-right: 20px; } } } } } }
/* ============================================================================
 * END Connect block
 * ============================================================================ */

